import React, { useState, useEffect } from 'react';
import AnimatedRadialGauge from './components/AnimatedRadialGauge';
import HistoryChart from './components/HistoryChart';
import ParticlesBackground from './components/ParticlesBackground';
import AnimatedHeader from './components/AnimatedHeader';
import EnhancedAlert from './components/EnhancedAlert';

// Enhanced progress bar gauge with gradient colors
const ProgressGauge = ({ value, max, label, unit }) => {
  const percentage = (value / max) * 100;
  let color = 'bg-gradient-to-r from-green-500 to-green-600';
  
  if (percentage > 80) color = 'bg-gradient-to-r from-red-500 to-red-600';
  else if (percentage > 60) color = 'bg-gradient-to-r from-yellow-500 to-yellow-600';

  return (
    <div className="bg-black bg-opacity-30 backdrop-blur-md rounded-2xl p-6 border border-white border-opacity-10 shadow-xl hover:shadow-2xl transition-all duration-300">
      <h2 className="text-xl font-semibold mb-4 text-center text-blue-100">{label}</h2>
      <div className="text-4xl font-bold text-center mb-4 text-white drop-shadow-lg">{value}{unit}</div>
      <div className="w-full bg-gray-700 bg-opacity-50 rounded-full h-4">
        <div 
          className={`${color} h-4 rounded-full transition-all duration-500 shadow-inner`}
          style={{ width: `${percentage}%` }}
        ></div>
      </div>
      <div className="flex justify-between mt-2 text-sm text-gray-300">
        <span>0{unit}</span>
        <span>{max}{unit}</span>
      </div>
    </div>
  );
};

function App() {
  const [carData, setCarData] = useState({
    speed: 0,
    fuel: 0,
    engineTemp: 0,
    rpm: 0,
    batteryVoltage: 0,
    tirePressure: { frontLeft: 0, frontRight: 0, rearLeft: 0, rearRight: 0 }
  });
  const [alerts, setAlerts] = useState([]);
  const [isConnected, setIsConnected] = useState(false);
  const [history, setHistory] = useState([]);
  const [lastUpdate, setLastUpdate] = useState(new Date());

  // Fetch data from backend
  const fetchCarData = async () => {
  try {
    const [statusResponse, historyResponse] = await Promise.all([
      fetch('https://car-health-backend-4.onrender.com/car/status'),
      fetch('https://car-health-backend-4.onrender.com/car/history')
    ]);
    
    if (!statusResponse.ok || !historyResponse.ok) {
      throw new Error('Network response was not ok');
    }
      
      const data = await statusResponse.json();
      const historyData = await historyResponse.json();
      
      setCarData(data);
      setHistory(historyData);
      setIsConnected(true);
      setLastUpdate(new Date());
      
      // Check for alerts
      const newAlerts = [];
      if (data.fuel < 20) {
        newAlerts.push({ type: 'fuel', message: ' Low Fuel  Please Refuel Soon.' });
      }
      if (data.engineTemp > 100) {
        newAlerts.push({ type: 'temp', message: ' Engine Overheating  Stop Vehicle.' });
      }
      if (data.batteryVoltage < 12.0) {
        newAlerts.push({ type: 'battery', message: ' Low Battery Voltage  Check Charging System.' });
      }
      if (Object.values(data.tirePressure || {}).some(pressure => pressure < 31.0)) {
        newAlerts.push({ type: 'tire', message: ' Low Tire Pressure  Check and Inflate Tires.' });
      }
      setAlerts(newAlerts);
    } catch (error) {
      console.error('Error fetching car data:', error);
      setIsConnected(false);
      
      // Fallback: Use simulated data when API is unavailable
      setCarData({
        speed: Math.floor(Math.random() * 120),
        fuel: Math.floor(Math.random() * 100),
        engineTemp: Math.floor(Math.random() * 50) + 70,
        rpm: Math.floor(Math.random() * 4000) + 600,
        batteryVoltage: Math.round((Math.random() * 2.4 + 11.8) * 10) / 10,
        tirePressure: {
          frontLeft: Math.round((Math.random() * 6 + 30) * 10) / 10,
          frontRight: Math.round((Math.random() * 6 + 30) * 10) / 10,
          rearLeft: Math.round((Math.random() * 6 + 30) * 10) / 10,
          rearRight: Math.round((Math.random() * 6 + 30) * 10) / 10
        }
      });
    }
  };

  useEffect(() => {
    fetchCarData(); // Initial fetch
    
    // Set up polling every 5 seconds
    const interval = setInterval(fetchCarData, 5000);
    
    return () => clearInterval(interval);
  }, []);

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 via-blue-900 to-gray-900 text-white p-6 relative overflow-hidden">
      <ParticlesBackground />
      
      {/* Animated background elements */}
      <div className="absolute inset-0 bg-grid-pattern opacity-10"></div>
      <div className="absolute top-0 left-0 w-72 h-72 bg-blue-500 rounded-full filter blur-3xl opacity-20 animate-pulse"></div>
      <div className="absolute bottom-0 right-0 w-96 h-96 bg-purple-500 rounded-full filter blur-3xl opacity-20 animate-pulse"></div>
      
      {/* Main content with glassmorphism effect */}
      <div className="relative z-10 bg-black bg-opacity-40 backdrop-blur-md rounded-3xl border border-white border-opacity-10 shadow-2xl p-6">
        <AnimatedHeader isConnected={isConnected} />
        
        {/* Alerts Section */}
        {alerts.length > 0 && (
          <div className="mb-8">
            <h2 className="text-2xl font-semibold mb-4 text-blue-100">Alerts</h2>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              {alerts.map((alert, index) => (
                <EnhancedAlert key={index} type={alert.type} message={alert.message} />
              ))}
            </div>
          </div>
        )}
        
        {/* Main Gauges */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
          <AnimatedRadialGauge value={carData.speed} max={120} label="Speed" unit=" km/h" warningThreshold={80} color="#6366f1" />
          <ProgressGauge value={carData.fuel} max={100} label="Fuel Level" unit="%" />
          <AnimatedRadialGauge value={carData.engineTemp} max={120} label="Engine Temperature" unit="C" warningThreshold={83} color="#ef4444" />
        </div>

        {/* Additional Metrics */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-8">
          <div className="bg-black bg-opacity-30 backdrop-blur-md rounded-2xl p-6 border border-white border-opacity-10 shadow-xl">
            <h2 className="text-xl font-semibold mb-4 text-center text-blue-100">RPM</h2>
            <div className="text-4xl font-bold text-center text-white drop-shadow-lg">{carData.rpm.toLocaleString()} RPM</div>
            <div className="w-full bg-gray-700 bg-opacity-50 rounded-full h-4 mt-4">
              <div 
                className="bg-gradient-to-r from-blue-500 to-blue-600 h-4 rounded-full transition-all duration-500 shadow-inner"
                style={{ width: `${(carData.rpm / 4000) * 100}%` }}
              ></div>
            </div>
            <div className="flex justify-between mt-2 text-sm text-gray-300">
              <span>0</span>
              <span>4000</span>
            </div>
          </div>
          
          <div className="bg-black bg-opacity-30 backdrop-blur-md rounded-2xl p-6 border border-white border-opacity-10 shadow-xl">
            <h2 className="text-xl font-semibold mb-4 text-center text-blue-100">Battery Voltage</h2>
            <div className="text-4xl font-bold text-center text-white drop-shadow-lg">{carData.batteryVoltage}V</div>
            <div className={`text-center mt-2 text-lg font-semibold ${
              carData.batteryVoltage < 12.0 ? 'text-red-400' : 
              carData.batteryVoltage < 12.5 ? 'text-yellow-400' : 'text-green-400'
            }`}>
              {carData.batteryVoltage < 12.0 ? 'Low' : 
              carData.batteryVoltage < 12.5 ? 'Fair' : 'Good'}
            </div>
            <div className="w-full bg-gray-700 bg-opacity-50 rounded-full h-4 mt-4">
              <div 
                className="bg-gradient-to-r from-green-500 to-green-600 h-4 rounded-full transition-all duration-500 shadow-inner"
                style={{ width: `${((carData.batteryVoltage - 11.8) / (14.2 - 11.8)) * 100}%` }}
              ></div>
            </div>
            <div className="flex justify-between mt-2 text-sm text-gray-300">
              <span>11.8V</span>
              <span>14.2V</span>
            </div>
          </div>
        </div>

        {/* Tire Pressure */}
        <div className="bg-black bg-opacity-30 backdrop-blur-md rounded-2xl p-6 border border-white border-opacity-10 shadow-xl mb-8">
          <h2 className="text-2xl font-semibold mb-4 text-center text-blue-100">Tire Pressure</h2>
          <div className="grid grid-cols-2 gap-4">
            {Object.entries(carData.tirePressure || {}).map(([position, pressure]) => (
              <div key={position} className="p-4 bg-gray-700 bg-opacity-50 rounded-lg text-center backdrop-blur-sm">
                <div className="font-semibold capitalize text-blue-100">{position.replace(/([A-Z])/g, ' $1')}</div>
                <div className={`text-2xl font-bold ${
                  pressure < 31.0 ? 'text-red-400' : 
                  pressure < 32.0 ? 'text-yellow-400' : 'text-green-400'
                }`}>
                  {pressure} PSI
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Historical Data Charts */}
        {history.length > 0 && (
          <div className="bg-black bg-opacity-30 backdrop-blur-md rounded-2xl p-6 border border-white border-opacity-10 shadow-xl mb-8">
            <h2 className="text-2xl font-semibold mb-4 text-blue-100">Historical Trends</h2>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <HistoryChart 
                data={history} 
                dataKey="speed" 
                name="Speed" 
                color="#8884d8" 
                unit=" km/h" 
              />
              <HistoryChart 
                data={history} 
                dataKey="fuel" 
                name="Fuel Level" 
                color="#82ca9d" 
                unit="%" 
              />
              <HistoryChart 
                data={history} 
                dataKey="engineTemp" 
                name="Engine Temperature" 
                color="#ff7f7f" 
                unit="C" 
              />
              <HistoryChart 
                data={history} 
                dataKey="rpm" 
                name="RPM" 
                color="#ffc658" 
                unit="" 
              />
            </div>
          </div>
        )}

        {/* System Status */}
        <div className="bg-black bg-opacity-30 backdrop-blur-md rounded-2xl p-6 border border-white border-opacity-10 shadow-xl mb-4">
          <h2 className="text-2xl font-semibold mb-4 text-blue-100">System Status</h2>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            <div className="text-center p-4 bg-gray-700 bg-opacity-50 rounded-lg backdrop-blur-sm">
              <div className="text-3xl font-bold text-green-400">{history.length}</div>
              <div className="text-gray-300">Data Points</div>
            </div>
            <div className="text-center p-4 bg-gray-700 bg-opacity-50 rounded-lg backdrop-blur-sm">
              <div className="text-3xl font-bold text-blue-400">5s</div>
              <div className="text-gray-300">Update Interval</div>
            </div>
            <div className="text-center p-4 bg-gray-700 bg-opacity-50 rounded-lg backdrop-blur-sm">
              <div className="text-3xl font-bold text-green-400">Online</div>
              <div className="text-gray-300">API Status</div>
            </div>
            <div className="text-center p-4 bg-gray-700 bg-opacity-50 rounded-lg backdrop-blur-sm">
              <div className="text-3xl font-bold text-purple-400">{new Date().toLocaleTimeString()}</div>
              <div className="text-gray-300">Current Time</div>
            </div>
          </div>
        </div>

        {/* Last Updated Indicator */}
        <div className="text-center text-blue-300 text-sm mt-6">
          Last updated: {lastUpdate.toLocaleTimeString()}
        </div>
      </div>
    </div>
  );
}

export default App;