import React from 'react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';

const HistoryChart = ({ data, dataKey, name, color, unit }) => {
  // Format data for the chart
  const chartData = data.map((item, index) => ({
    name: `Reading ${index + 1}`,
    value: item[dataKey],
    timestamp: new Date(item.timestamp).toLocaleTimeString()
  }));

  return (
    <div className="bg-gray-800 rounded-xl p-4 h-80">
      <h3 className="text-lg font-semibold mb-4 text-center">{name} History</h3>
      <ResponsiveContainer width="100%" height="100%">
        <LineChart data={chartData}>
          <CartesianGrid strokeDasharray="3 3" stroke="#444" />
          <XAxis 
            dataKey="timestamp" 
            stroke="#ccc"
            angle={-45}
            textAnchor="end"
            height={60}
          />
          <YAxis stroke="#ccc" />
          <Tooltip 
            contentStyle={{ backgroundColor: '#2d3748', border: 'none' }}
            formatter={(value) => [`${value}${unit}`, name]}
          />
          <Line 
            type="monotone" 
            dataKey="value" 
            stroke={color} 
            activeDot={{ r: 6 }} 
            strokeWidth={2}
          />
        </LineChart>
      </ResponsiveContainer>
    </div>
  );
};

export default HistoryChart;