import React from 'react';

const EnhancedAlert = ({ type, message }) => {
  const alertConfig = {
    fuel: {
      icon: '',
      gradient: 'from-yellow-600 to-orange-600',
      border: 'border-yellow-500',
      iconColor: 'text-yellow-300'
    },
    temp: {
      icon: '',
      gradient: 'from-red-600 to-pink-600',
      border: 'border-red-500',
      iconColor: 'text-red-300'
    },
    battery: {
      icon: '',
      gradient: 'from-amber-600 to-yellow-600',
      border: 'border-amber-500',
      iconColor: 'text-amber-300'
    },
    tire: {
      icon: '',
      gradient: 'from-orange-600 to-red-600',
      border: 'border-orange-500',
      iconColor: 'text-orange-300'
    }
  };

  const config = alertConfig[type] || alertConfig.fuel;

  return (
    <div className={`bg-gradient-to-r ${config.gradient} rounded-xl p-4 border ${config.border} shadow-lg transform hover:scale-105 transition-transform duration-200`}>
      <div className="flex items-center">
        <span className={`text-2xl mr-3 ${config.iconColor}`}>{config.icon}</span>
        <div>
          <p className="font-semibold text-white drop-shadow-md">{message}</p>
        </div>
      </div>
    </div>
  );
};

export default EnhancedAlert;