import React, { useState, useEffect } from 'react';

const AnimatedHeader = ({ isConnected }) => {
  const [currentText, setCurrentText] = useState(0);
  const texts = [
    "Real-time vehicle status monitoring system",
    "Advanced car health analytics",
    "Predictive maintenance insights"
  ];

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentText((prev) => (prev + 1) % texts.length);
    }, 3000);
    return () => clearInterval(interval);
  }, [texts.length]);

  return (
    <header className="text-center mb-8 relative">
      <h1 className="text-5xl font-bold bg-gradient-to-r from-blue-400 to-purple-500 bg-clip-text text-transparent drop-shadow-lg mb-4">
        Car Health Monitoring Dashboard
      </h1>
      <div className="h-8">
        <p className="text-blue-200 text-lg transition-opacity duration-500">
          {texts[currentText]}
        </p>
      </div>
      <div className={`inline-block px-4 py-2 rounded-full text-sm mt-4 ${
        isConnected 
          ? 'bg-gradient-to-r from-green-500 to-green-600 text-white shadow-lg' 
          : 'bg-gradient-to-r from-red-500 to-red-600 text-white shadow-lg'
      } transition-all duration-300`}>
        <span className="flex items-center">
          <span className={`w-3 h-3 rounded-full mr-2 ${
            isConnected ? 'bg-green-300 animate-pulse' : 'bg-red-300'
          }`}></span>
          {isConnected ? 'Connected to API' : 'Disconnected from API'}
        </span>
      </div>
    </header>
  );
};

export default AnimatedHeader;