import React, { useState, useEffect } from 'react';
import { RadialBarChart, RadialBar, ResponsiveContainer } from 'recharts';

const AnimatedRadialGauge = ({ value, max, label, unit, warningThreshold = 80, color = '#3b82f6' }) => {
  const [animatedValue, setAnimatedValue] = useState(0);
  
  useEffect(() => {
    const timer = setTimeout(() => {
      setAnimatedValue(value);
    }, 100);
    return () => clearTimeout(timer);
  }, [value]);

  const data = [{ value: animatedValue, fill: animatedValue > max * (warningThreshold/100) ? '#ef4444' : color }];
  
  return (
    <div className="bg-black bg-opacity-30 backdrop-blur-md rounded-2xl p-6 border border-white border-opacity-10 shadow-xl hover:shadow-2xl transition-all duration-300 hover:scale-105">
      <h2 className="text-xl font-semibold mb-4 text-center text-blue-100">{label}</h2>
      <div className="text-4xl font-bold text-center mb-2 text-white drop-shadow-lg">
        {animatedValue}{unit}
      </div>
      <ResponsiveContainer width="100%" height={200}>
        <RadialBarChart 
          innerRadius="70%" 
          outerRadius="100%" 
          data={data} 
          startAngle={180} 
          endAngle={0}
        >
          <RadialBar 
            minAngle={15} 
            background 
            clockWise 
            dataKey='value' 
            cornerRadius={10}
          />
        </RadialBarChart>
      </ResponsiveContainer>
      <div className="flex justify-between mt-2 text-sm text-gray-300">
        <span>0{unit}</span>
        <span>{max}{unit}</span>
      </div>
      {animatedValue > max * (warningThreshold/100) && (
        <div className="mt-2 text-red-400 font-semibold text-center animate-pulse">
           Warning!
        </div>
      )}
    </div>
  );
};

export default AnimatedRadialGauge;